import FileItem from "jx/editor/editionitems/FileItem";
export default class VideoItem extends FileItem {
    constructor(dataObject) {
        dataObject.data.asset.type = "Video";
        super(Object.assign({
            icoSymbol: "film",
            actions: ["download", "preview"],
            informations: ["asset"],
            backgroundColor: "#c7cbff"
        }, dataObject));
    }
}
